import { http } from '../utils/http';
import { handleResponse, handleError } from './ServiceUtils';

export const batchServices = () => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', ``, true)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
export const getBatchBybatchId = (id) => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'GET', `batches-service/batches/${id}`, true)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

