export const addFeedbackByModuleId = (moduleId,payload) => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_BATCHES, 'POST', `batches-service/feedbacks/modules/${moduleId}`, payload)
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};


