import React, { useState, useEffect } from 'react';
import Moment from 'react-moment';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider } from '../../../components/hook-form';
import * as yup from 'yup';
import { useDispatch, useSelector } from 'react-redux';
import { getAllModuleTransaction, updateTransactionComment } from '../../../store/actions/TransactionActions';
import { withStyles } from '@material-ui/styles';
import { setMessage } from '../../../store/actions/MessageActions';
import {
    Grid,
    TextField,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    CircularProgress,
    Box,
    InputAdornment,
    Checkbox
} from '@material-ui/core';

import SchoolIcon from '@material-ui/icons/School';
import { IconSearch } from '@tabler/icons';
import useStyles from '../../ViewTableStyles';
import useUserRole from '../../../hooks/useUserRole';
import { getModuleTransactionExport } from '../../../services/export';
import { Alert, Snackbar, useMediaQuery } from '@mui/material';
import MainCard from '../../../ui-component/cards/MainCard';
import ViewBatchIdsPopUp from '../../subscriptions/viewSubscription/viewBatchIdsPopUp';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const validationSchema = yup.object({
    startDeliveryDate: yup.string().required('Start Date is required'),
    endDeliveryDate: yup.string().required('End Date is required')
});

export default function ModuleTransactions() {
    const classes = useStyles();
    const isSmallScreen = useMediaQuery('(max-width: 600px)');
    useUserRole({ roleList: ['ADMIN','SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const [isSearch, setSearch] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);

    const { getAllModuleTransactionData } = useSelector((state) => state?.transaction);
    const newData = getAllModuleTransactionData?.content;
    const totalCount = getAllModuleTransactionData?.totalElements;

    const [startDate, setStartDate] = useState(new Date().toISOString().substring(0, 10));
    const [endDate, setEndDate] = useState(new Date().toISOString().substring(0, 10));
    const [loading, setLoading] = useState(false);
    const [dowmloadFail, setDownloadFail] = useState(false);
    const [selected, setSelected] = useState([]);
    const [batchIdData, setBatchIdData] = useState([]);
    const [openBachIdPopup, setOpenBachIdPopup] = useState(false);
    const [isCommentEmpty, setCommentEmpty] = useState(true);

    const isSelected = (name) => selected.indexOf(name) !== -1;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleSearch = (event) => {
        setSearch(event.target.value);
    };

    const initialValues = {
        startDeliveryDate: '',
        endDeliveryDate: ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });
    const { reset, handleSubmit } = methods;

    const values = {
        someDate: new Date().toISOString().substring(0, 10)
    };
    const exportTransaction = async () => {
        setDownloadFail(false);
        setLoading(true);

        await getModuleTransactionExport(selected)
            .then((r) => {
                setLoading(false);
            })
            .catch((error) => {
                setLoading(false);
                setDownloadFail(true);
                console.log(error);
            });
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
            const newSelecteds = newData.map((n) => n.id);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const onSubmit = async (data) => {
        dispatch(
            getAllModuleTransaction(
                page + 1,
                rowsPerPage,
                startDate,
                endDate,
                isSearch ? isSearch : '',

                [page, rowsPerPage, startDate, endDate, isSearch]
            )
        );
    };

    const [comments, setComments] = useState({});
    const updateComment = (rowId, comment) => {
        setComments((prevComments) => {
            const updatedComments = { ...prevComments };
            updatedComments[rowId] = comment;
            setCommentEmpty(comment.trim() === '');
            return updatedComments;
        });
    };

    const submitForm = async (id, comment) => {
        let payload = { comment: comment };

        await updateTransactionComment(payload, id)
            .then((data) => {
                dispatch(setMessage(data?.message));
                dispatch(getAllModuleTransaction(page + 1, rowsPerPage, startDate, endDate, isSearch ? isSearch : ''));
            })
            .catch((error) => {
                dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something went wrong while commenting'));
            });
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);

        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
        }

        setSelected(newSelected);
    };

    useEffect(() => {
        dispatch(getAllModuleTransaction(page + 1, rowsPerPage, startDate, endDate, isSearch ? isSearch : ''));
    }, [page, rowsPerPage, startDate, endDate, isSearch]);

    const toggleDropdown = (data) => {
        setOpenBachIdPopup(!openBachIdPopup);
        setBatchIdData(data);
    };

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SchoolIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Module Transactions </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container justifyContent="space-between">
                        {isSmallScreen ? ( // if I want to see mobile screens size , 
                            <>
                                <Grid item xs={12} style={{ marginBottom: '20px' }}>
                                    <OutlinedInput
                                        className={classes.searchControl}
                                        id="input-search-header"
                                        placeholder="Search"
                                        onChange={(event) => handleSearch(event)}
                                        style={{ width: '100%' }}
                                        startAdornment={
                                            <InputAdornment position="start">
                                                <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                            </InputAdornment>
                                        }
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                                        <div>
                                            <label htmlFor="startDeliveryDate">From:</label>
                                            <TextField
                                                type="date"
                                                id="startDeliveryDate"
                                                name="startDeliveryDate"
                                                defaultValue={values.someDate}
                                                onChange={(e) => setStartDate(e.target.value)}
                                                fullWidth
                                            />
                                        </div>
                                        <div style={{ marginTop: '10px' }}>
                                            <label htmlFor="endDeliveryDate">To:</label>
                                            <TextField
                                                type="date"
                                                id="endDeliveryDate"
                                                name="endDeliveryDate"
                                                defaultValue={values.someDate}
                                                onChange={(e) => setEndDate(e.target.value)}
                                                fullWidth
                                            />
                                        </div>
                                        <div style={{ marginTop: '10px' }}>
                                            <Button variant="contained" className={classes.btn} onClick={onSubmit} type="submit" fullWidth>
                                                View
                                            </Button>
                                        </div>
                                    </FormProvider>
                                </Grid>
                                &nbsp;
                                <Grid item xs={12} sm={2}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9', marginBottom: '20px' }}
                                        onClick={exportTransaction}
                                        fullWidth
                                    >
                                        Export
                                    </Button>
                                </Grid>
                            </>
                        ) : (
                            // if I want to see web screens size 
                            <>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    placeholder="Search"
                                    onChange={(event) => handleSearch(event)}
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                            <Grid>
                                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                                    <Grid style={{ display: 'flex' }}>
                                        <div>
                                            From: &nbsp;
                                            <TextField
                                                type="date"
                                                name="startDeliveryDate"
                                                defaultValue={values.someDate}
                                                onChange={(e) => setStartDate(e.target.value)}
                                            />
                                            &nbsp; To: &nbsp;
                                            <TextField
                                                type="date"
                                                name="endDeliveryDate"
                                                defaultValue={values.someDate}
                                                onChange={(e) => setEndDate(e.target.value)}
                                            />
                                        </div>
                                        &nbsp;&nbsp; &nbsp;&nbsp;
                                        <div>
                                            <Button
                                                variant="contained"
                                                className={classes.btn}
                                                onClick={onSubmit}
                                                type="submit"
                                                style={{ alignItems: 'center' }}
                                            >
                                                View
                                            </Button>
                                        </div>
                                    </Grid>
                                </FormProvider>
                            </Grid>
                            <Grid sm={2} xs={12} item display="flex" justifyContent="center">
                                <Button
                                    variant="contained"
                                    color="primary"
                                    style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9', marginBottom: '50px' }}
                                    onClick={exportTransaction}
                                >
                                    Export
                                </Button>
                            </Grid>
                        </>
                        )}
                    </Grid>
                    <Grid sm={2} xs={12} item display="flex" justifyContent="center">
                        {loading ? (
                            <>
                                <h7>Downloading...</h7>{' '}
                                <Box sx={{ display: 'flex' }}>
                                    <CircularProgress />
                                </Box>
                            </>
                        ) : (
                            <></>
                        )}
                        {dowmloadFail ? <Alert severity="error">Downloading Failed !</Alert> : <> </>}
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell padding="checkbox">
                                        <Checkbox
                                            indeterminate={selected?.length > 0 && selected?.length < newData?.length}
                                            checked={newData?.length > 0 && selected?.length === newData?.length}
                                            onChange={handleSelectAllClick}
                                            inputProps={{ 'aria-label': 'select all items' }}
                                        />
                                    </TableCell>
                                    <TableCell> Date </TableCell>
                                    <TableCell> Amount Details </TableCell>
                                    <TableCell> User Details </TableCell>
                                    <TableCell> Course Details </TableCell>
                                    <TableCell> Module Details </TableCell>
                                    <TableCell> Batch Ids </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {newData?.map((row, index) => {
                                        const isItemSelected = isSelected(row?.id);

                                        const labelId = `enhanced-table-checkbox-${index}`;
                                        return (
                                            <StyledTableRow key={row?.id}>
                                                <TableCell padding="checkbox">
                                                    <Checkbox
                                                        checked={isItemSelected}
                                                        inputProps={{ 'aria-labelledby': labelId }}
                                                        onChange={(event) => handleClick(event, row?.id)}
                                                    />
                                                </TableCell>
                                                <TableCell style={{ fontSize: '15px' }}>
                                                    <Moment format="YYYY/MM/DD" date={row?.createdAt} />
                                                </TableCell>
                                                <TableCell style={{ fontSize: '15px' }}>
                                                    <b> Order ID&nbsp;- &nbsp;</b> {row?.orderId}
                                                    <br />
                                                    <b> Amount&nbsp;-&nbsp;</b> {'â‚¹'}&nbsp;{row?.pricePaid ? row?.pricePaid : 'NA'}
                                                    <br />
                                                    <b> Tranx ID&nbsp;-&nbsp; </b>
                                                    {row?.paymentId ? row?.paymentId : 'NA'}
                                                    <br />
                                                    <b> Mode&nbsp;-&nbsp;</b>
                                                    {row?.paymentMode.replace(/_/g, ' ').charAt(0).toUpperCase() +
                                                        row?.paymentMode.slice(1).toLowerCase()}
                                                    <br />
                                                    <b> Payment Status&nbsp;-&nbsp;</b> {row?.paymentStatus ? row?.paymentStatus : 'NA'}{' '}
                                                    <br />
                                                    <b> Discount Code&nbsp;-&nbsp;</b> {row?.discountCode ? row?.discountCode : 'NA'} <br />
                                                    <b> Gift Voucher Code &nbsp;-&nbsp;</b>{' '}
                                                    {row?.giftVoucherCode ? row?.giftVoucherCode : 'NA'}
                                                </TableCell>
                                                <TableCell style={{ fontSize: '15px' }}>
                                                    <b>Name (Cus Id) - </b> {row?.userLimitedInfo?.fullName}&nbsp;(
                                                    {row?.userLimitedInfo?.profileId})
                                                    <br />
                                                    <b>Email - </b> {row?.userLimitedInfo?.email}
                                                    <br />
                                                    <b>Mobile - </b> {row?.userLimitedInfo?.mobile}
                                                    <br />
                                                    <b>Tranx Comment - </b> {row?.comment}
                                                    <br />
                                                    <TextField
                                                        id={`comment-${row?.id}`}
                                                        label="Enter your comment..."
                                                        variant="outlined"
                                                        style={{ width: '60%', height: '30px' }}
                                                        value={comments[row?.id] || ''}
                                                        onChange={(event) => updateComment(row?.id, event.target.value)}
                                                    />
                                                    <Button
                                                        style={{ width: '20%', height: '10px' }}
                                                        disabled={isCommentEmpty}
                                                        onClick={() => {
                                                            submitForm(row?.id, comments[row?.id]);
                                                        }}
                                                    >
                                                        Update
                                                    </Button>
                                                </TableCell>
                                                <TableCell style={{ fontSize: '15px' }}>
                                                    <b> {row?.courseLimitedInfo?.title} </b>
                                                    <br />
                                                </TableCell>
                                                <TableCell style={{ fontSize: '15px' }}>
                                                    <b> {row?.moduleLimitedInfo?.title} </b>
                                                </TableCell>
                                                <TableCell style={{ fontSize: '15px' }}>
                                                    {row?.batchId?.length > 0 && (
                                                        <Button
                                                            variant="contained"
                                                            color="secondary"
                                                            size="small"
                                                            onClick={() => toggleDropdown(row?.batchId)}
                                                        >
                                                            BatchIds
                                                        </Button>
                                                    )}
                                                </TableCell>
                                            </StyledTableRow>
                                        );
                                    })}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={totalCount || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openBachIdPopup && <ViewBatchIdsPopUp data={batchIdData} open={openBachIdPopup} close={setOpenBachIdPopup} />}
            </MainCard>
        </>
    );
}
