import React from 'react';
import { Button, Dialog, DialogTitle, DialogContent, DialogActions, Typography, Link, Divider, Paper, TableRow } from '@mui/material';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from '../../../ViewDialogStyles';
import { useState } from 'react';
import { getBatchBybatchId } from '../../../../services/batchServices';
import { useEffect } from 'react';
import { withStyles } from '@material-ui/styles';
import { CircularProgress, Grid, Table, TableBody, TableCell, TableContainer, TableHead } from '@mui/material';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function ViewBatchIdsPopUp(props) {
    const { open, close, data } = props;
    const [batches, setBatches] = useState(null);
    const [loading, setLoading] = useState(true);
    const classes = useStyles();
    const handlePopupClose = () => {
        close(false);
    };
    const fetchBatchData = async (batchId) => {
        try {
            const res = await getBatchBybatchId(batchId);
            setBatches(res );
            setLoading(false);
        } catch (error) {
            console.log(error);
            setLoading(false);
        }
    };
    useEffect(() => {
        if (data[0]) {
            fetchBatchData(data[0]);
        }
    }, [data]);
    const formatStartDateTime = (dateTime) => {
        const formattedDateTime = new Date(dateTime).toLocaleString();
        return formattedDateTime;
    };
    return (
        <div>
            <Dialog open={open} maxWidth={'sm'}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: 'px' }}>Batch Ids</Typography>
                        <Link to="#" onClick={handlePopupClose} style={{ fontSize: '16px' }}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <DialogContent style={{ maxHeight: '350px' }}>
                    <Grid container>
                        <TableContainer>
                            <Table aria-label="simple table">
                                <TableHead className={classes.tableHeaderCell}>
                                    <StyledTableRow>
                                        <TableCell align="center"> Batch Id</TableCell>
                                        <TableCell align="center"> Batch Name</TableCell>
                                        <TableCell align="center"> Max Age</TableCell>
                                        <TableCell align="center"> Max Student</TableCell>
                                        <TableCell align="center"> Min Age</TableCell>
                                        <TableCell align="center"> Student Count</TableCell>
                                        <TableCell align="center"> Teacher Count </TableCell>
                                        <TableCell align="center"> StartDate Time </TableCell>
                                    </StyledTableRow>
                                </TableHead>
                                {loading ? (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell colSpan={8} style={{ textAlign: 'center' }}>
                                                <div
                                                    style={{
                                                        display: 'flex',
                                                        justifyContent: 'center',
                                                        alignItems: 'center',
                                                        height: '200px'
                                                    }}
                                                >
                                                    <CircularProgress />
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                ) : batches?.length === 0 ? (
                                    <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                                ) : (
                                    <TableBody>
                                       
                                            <TableRow key={batches.id}>
                                                <TableCell align="center">{batches?.id}</TableCell>
                                                <TableCell align="center">{batches?.name}</TableCell>
                                                <TableCell align="center">{batches?.maxAge}</TableCell>
                                                <TableCell align="center">{batches?.maxStudent}</TableCell>
                                                <TableCell align="center">{batches?.minAge}</TableCell>
                                                <TableCell align="center">{batches?.studentCount}</TableCell>
                                                <TableCell align="center">{batches?.teacherCount}</TableCell>
                                                <TableCell align="center">{formatStartDateTime(batches?.startDateTime)}</TableCell>
                                            </TableRow>
                                        
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Grid>
                </DialogContent>

                <Divider style={{ marginTop: 15 }} />
                <DialogActions className={classes.dialogAction}>
                    <Button variant="outlined" color="primary" className={classes.buttonCancel} onClick={handlePopupClose}>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
