import { http } from '../../../utils/http';
import { GET_ALL_TRANSACTION_START, GET_ALL_TRANSACTION_SUCCESS, GET_ALL_TRANSACTION_ERROR } from '../../constants';
import { setMessage } from '../MessageActions';

const getAllTransactionStart = () => {
    return { type: GET_ALL_TRANSACTION_START };
};
const getAllTransactionSuccess = (payload) => {
    return { type: GET_ALL_TRANSACTION_SUCCESS, payload };
};
const getAllTransactionError = (error) => {
    return { type: GET_ALL_TRANSACTION_ERROR, error };
};

const getAllTransaction =
    (page = 1, rowsPerPage = 10, startDate = '', endDate = '', searchData = '') =>
    (dispatch) => {
        dispatch(getAllTransactionStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
                'GET',
                `subscriptions-service/transactions?pageNumber=${page}&pageSize=${rowsPerPage}&startDate=${startDate}&endDate=${endDate}&userSearchKey=${searchData}&planSearchKey=${searchData}&moduleSearchKey=${searchData}`,

                true
            )
                .then((res) => {
                    const payload = {
                        getAllTransactionData: res
                    };
                    dispatch(getAllTransactionSuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllTransactionError(err));
                    dispatch(setMessage(err?.data?.message));
                });
        } catch (error) {
            dispatch(getAllTransactionError('Something went wrong'));
        }
    };

    const getAllModuleTransaction =
    (page = 1, rowsPerPage = 10, startDate = '', endDate = '', searchData = '') =>
    (dispatch) => {
        dispatch(getAllTransactionStart());
        try {
            http(
                process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS,
                'GET',
                `subscriptions-service/transactions/module?pageNumber=${page}&pageSize=${rowsPerPage}&startDate=${startDate}&endDate=${endDate}&userSearchKey=${searchData}&planSearchKey=${searchData}&moduleSearchKey=${searchData}`,

                true
            )
                .then((res) => {
                    const payload = {
                        getAllModuleTransactionData: res
                    };
                    dispatch(getAllTransactionSuccess(payload));
                })
                .catch((err) => {
                    dispatch(getAllTransactionError(err));
                    dispatch(setMessage(err?.data?.message));
                });
        } catch (error) {
            dispatch(getAllTransactionError('Something went wrong'));
        }
    };

    export const updateTransactionComment = (payload, id) => {
        return new Promise((resolve, reject) => {
            http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'PUT', `subscriptions-service/transactions/${id}`, payload, true)
                .then((data) => {
                    resolve(data);
                })
                .catch((err) => {
                    reject(err);
                });
        });
    };
    

export { getAllTransaction, getAllModuleTransaction };
